@import url('https://fonts.googleapis.com/css2?family=Red+Rose&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Averia+Gruesa+Libre&display=swap');

body {
  margin: 0;
  font-family: 'Averia Gruesa Libre', sans-serif;
  color: white;
}

.page-text {
  position: absolute;
  width: 40%;
  white-space: pre-line;
}

.page-text img {
  vertical-align: middle;
  margin-right: 0.3em;
}

.illust {
  position: absolute;
  width: 45%;
}

@media (max-aspect-ratio: 1/1) {
  .page-text {
    width: 90%;
  }
  .illust {
    margin-top: 50%;
    width: 90%;
  }
}